* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html {
  width: 100%;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100%;
  height: 100%; }

h1 {
  margin: 0; }

@font-face {
  font-family: 'Merriweather';
  src: url("assets/fonts/Merriweather/Merriweather-Regular.ttf") format("ttf");
  font-weight: normal;
  font-style: normal; }

.top-nav {
  background-color: #90d465;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  width: 100%;
  box-shadow: 0 -25px 40px 30px rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  right: 0; }
  .top-nav h1 {
    color: white;
    font-family: 'Merriweather';
    text-shadow: 1px 1px 1px #005c14; }
  .top-nav + * {
    padding-top: 60px; }

.product-listing {
  display: flex;
  flex-wrap: wrap; }
  .product-listing .product-listing-item {
    flex: 0 0 50%; }

.product-listing-item .image {
  width: 100%;
  height: 100%;
  background-size: cover; }

.product-listing-item:after {
  content: '';
  display: block;
  padding-bottom: 100%; }

.app {
  width: 100%;
  height: 100%; }



